import React, { Component } from "react";
import { data } from "./Data";
import { updateRenderState } from ".";
import ItemViewer from "./ItemViewer";

export default class SearchItems extends Component {
	clickHandel = _ => {
		console.log(data.filter(e => e.id === this.props.id)[0]);
		updateRenderState(<ItemViewer data={data.filter(e => e.id === this.props.id)[0]}/>);
		window.history.pushState(null, null, `/search#${window.location.hash.split("#")[1]}#${this.props.id}`);
	};
	checkIcons = _ => {
		if (this.props.description === "فاكهة") {
			return "url(/assets/itemsCategoriesIcons/fruits.png)";
		}
	};
	render() {
		return (
				<div className='searchItemsContainer' onClick={this.clickHandel}>
					<div className='searchItemsCategoriesIcon' style={{backgroundImage: this.checkIcons()}}></div>
					<div>
						<p className='searchItemsName'>{this.props.name}</p>
						<p className='searchItemsCategories'>{this.props.description}</p>
						<p className='searchItemsInfo'>
							{this.props.kcal} سعرة حرارية
							<span> • </span>
							{this.props.serving}
						</p>
					</div>
				</div>
		);
	}
}
